@import "../../../styles/variables"
@import "../../../styles/mixins"

.SuperCard

  width: 343px
  background: #FFFFFF
  border-radius: 25px

  margin: 20px auto 0


  display: flex
  align-items: center
  flex-direction: column


.CardTitle
  font-family: $font-dela
  font-size: 24px
  background: $font-gradient
  line-height: 150%
  letter-spacing: -0.022em
  +gradient-text

  margin: 20px 0 0

.CardSubTitle
  margin: 10px 0 0
  width: calc(100% - 12px)
  font-family: $font-mont
  font-weight: 500
  font-size: 14px
  line-height: 120%
  text-align: center
  letter-spacing: -0.022em
.CardImage
  width: 303px
  height: 153px
  margin-top: 15px
  img
    object-fit: contain
    width: 303px
    height: 153px

.Button
  color: #FFFFFF
  font-family: $font-dela
  font-size: 28px

.ButtonWrapper
  width: 283px
  margin: 0 0 25px

@media screen and (max-width: 350px)
  .SuperCard
    width: 95%

  .CardImage
    width: 283px
    height: 143px
    img
      width: 283px
      height: 143px

  .ButtonWrapper
    width: 283px
