@import "../../../../../styles/variables"
@import "../../../../../styles/mixins"

.Win
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column

  width: 100%
  min-height: 100vh

.BackgroundImage
  width: 100vw
  height: 100vh
  top: 0
  left: 0
  position: fixed
  z-index: 0

.WinAccent
  width: 100vw
  top: calc(50vh)
  transform: translateY(-50%)
  left: 0
  position: fixed
  z-index: 0

.sberLogo
  width: 232px
  margin-left: calc((100vw - 232px) / 2)
  position: fixed
  z-index: 1
  top: 45px
  left: 0

.Content
  display: flex
  flex-direction: column
  align-items: center

  text-align: center

  position: relative
  z-index: 1


.TopText
  top: 100px

.SubscribeText, .AccentText, .SubTitle
  font-family: $font-mont
  color: #FFFFFF
  margin: 0
.SubscribeText
  font-size: 20px
  font-weight: 600

.SubTitle
  font-size: 14px
  font-weight: 500
  letter-spacing: -0.022em
  opacity: .8


.AccentText
  font-size: 32px
  font-weight: 700
.BottomText, .TopText
  width: calc(100vw - 32px)
  max-width: 375px

  position: fixed

.BottomText
  bottom: 109px

  text-align: center
  font-family: $font-mont
  font-size: 8px
  color: #FFFFFF

.Button
  text-decoration: none
  position: fixed
  top: calc(50vh + 90px)

  +center-flex

  width: 300px
  height: 72px

  transform: rotate(-1.2deg) translateY(-50%)

  font-family: $font-mont
  font-size: 28px
  font-weight: 700

  color: #428050

  background: #FFFFFF
  border-radius: 16px


@media screen and (max-width: 350px)
  .BottomText
    font-size: 6px
  .TopText
    width: calc(100vw - 16px)

