@import "../../../styles/variables"
@import "../../../styles/mixins"

.BackgroundImage
  width: 100vw
  top: 0
  left: 0
  position: fixed
  z-index: 0

.SuperGameWrapper
  position: relative

  display: flex
  align-items: center
  justify-content: center
  flex-direction: column

  width: 100%
  min-height: 100vh

  background-color: $background-color
.SuperGameContent, .Condition, .Marker
  display: flex
  font-family: $font-mont
  font-size: 14px
  font-weight: 500
  color: $font-color
  line-height: 120%

.SuperGameContent
  display: flex
  flex-direction: column
  align-items: center

  text-align: center

  margin-bottom: 20px

  
.Title
  padding-bottom: 20px
  padding-top: 60px

  margin-bottom: 12px
  font-family: $font-dela
  font-size: 40px

  text-align: center
  background: $font-gradient
  +gradient-text

.SuperGameBlock
  display: flex
  align-items: center
  flex-direction: column
  padding: 10px 20px 20px

  background: #FFFFFF
  border-radius: 25px

  margin-top: 18px


.Condition

  display: flex
  align-items: center
  font-size: 14px
  margin-top: 15px

  text-align: left

  width: calc(100vw - 90px)

  p
    margin: 0
  a
    color: $secondary-color
    font-weight: 700

.Marker
  color: #FFFFFF
  font-size: 14px

  background: $accent-gradient-purple
  border-radius: 50%

  min-width: 20px
  height: 20px
  +center-flex

  margin-right: 8px

.AccentButton
  color: #FFFFFF
  font-family: $font-dela
  font-size: 28px
  margin: 25px 0

.AccentButtonWrapper
  width: calc(100vw - 90px)
  margin-top: 20px
.WrapperInput
  margin-top: 20px
  +center-flex
  flex-direction: column

  width: calc(100vw - 90px)
  padding: 16px 0

  background: $background-color
  border-radius: 20px

.Input
  resize: none
  font-family: $font-mont
  font-size: 12px
  line-height: 150%

  color: $font-color
  background: $background-color
  min-width: calc(100vw - 130px)
  min-height: 36px
  max-width: calc(100vw - 130px)
  max-height: 36px
  white-space: pre-line

  border: none

.InputButton
  +center-flex
  text-decoration: none
  background-color: #FFFFFF
  border-radius: 15px

  width: calc(100vw - 110px)
  height: 42px

  margin-top: 13px
  p
    font-family: $font-mont
    font-size: 16px
    font-weight: 600

    background: $accent-gradient-purple
    +gradient-text

@media screen and (max-width: 345px)
  .AccentButton
    font-size: 26px
  .Input
    min-height: 56px
    max-height: 56px