@import "../../../styles/variables"
@import "../../../styles/mixins"

.Card
  cursor: pointer

  width: 343px
  height: 139px

  display: flex
  align-items: center
  justify-content: space-between

  border-radius: 24px
  margin: 16px auto 0

  background: #FFFFFF

  overflow-y: hidden


.CardTitle
  min-width: 150px
  max-width: 150px


  font-family: $font-dela
  font-size: 14px
  background: $font-gradient
  line-height: 150%
  letter-spacing: -0.022em
  +gradient-text
  margin: 0 0 10px

.CardContent
  margin-left: 24px
  font-family: $font-mont
  font-weight: 500
  font-size: 14px
  color: #313234

.CardImage
  width: 125px
  height: 139px
  img
    object-fit: cover
    width: 125px
    height: 139px


.VerylongArrow
  position: absolute
  margin-left: 64%

@media screen and (max-width: 350px)
  .Card
    width: 95%
  .CardTitle
    font-size: 14px
    min-width: 123px