@import "../../../styles/variables"
@import "../../../styles/mixins"

.Home
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  min-height: 100vh

  background: $background-color
  padding-bottom: 120px

  &Title
    margin: 0
    position: relative
    z-index: 1

    display: flex
    flex-direction: column
    align-items: center
    p
      margin: 0
      font-family: $font-dela
      font-size: 48px
      background: $font-gradient
      +gradient-text

    padding-top: 100px
    padding-bottom: 20px

    &Accent
      +center-flex
      
      background: #FFFFFF
      border-radius: 20px
      width: 337px
      height: 56px
      
      margin-top: -2px
      transform: rotate(2deg)

      p
        font-family: $font-mont
        font-weight: 700
        font-size: 20px
        background: $accent-gradient-purple
        +gradient-text

.BackgroundImage
  display: flex
  width: 100vw
  top: 20px
  left: 0
  position: fixed
  z-index: 0


.ButtonWrapper
  margin-top: 200px

@media screen and (max-width: 350px)
  .HomeTitle
    p
      font-size: 42px
    &Accent
      width: 307px
      p
        font-size: 18px