@import "../../../styles/variables"
@import "../../../styles/mixins"

.TestMainWrapper
  width: 100vw
.TestMainImage
  position: relative
  z-index: 1

  width: 100vw
  height: 174px
  img
    object-fit: cover
    width: 100vw
    height: 174px

.TestMain
  display: flex
  align-items: center
  flex-direction: column

  min-height: 100vh

  background: $background-color

.ContentWrapper
  width: calc(100vw - 32px)
  padding-top: 15px
  margin-bottom: 30px



.TestMainSubTitle, .Counter, .TestMainCondition, .TestMainMarker
  font-family: $font-mont
  font-weight: 500
  color: $font-color
.Counter
  font-size: 14px
  margin-bottom: 5px

.TestMainSubTitle
  font-size: 16px


.TestMainTitle
  padding-bottom: 7px
  font-family: $font-dela
  font-size: 24px
  line-height: 100%

  background: $font-gradient
  +gradient-text

  margin-bottom: 15px

.Separator
  display: block
  background: $font-color
  opacity: .1
  width: 90vw
  height: 1px

  margin-top: 25px
  margin-bottom: 15px

.TestMainCondition
  display: flex
  align-items: center
  font-size: 14px
  margin-top: 15px

  p
    margin: 0
  a
    color: $secondary-color
    font-weight: 700

.TestMainMarker
  color: #FFFFFF
  font-size: 14px

  background: $accent-gradient-purple
  border-radius: 50%

  min-width: 20px
  height: 20px
  +center-flex

  margin-right: 8px

.Button
  font-family: $font-dela
  color: #FFFFFF
  font-size: 28px
  margin: 20px

.ButtonWrapper
  position: fixed
  display: flex
  width: calc(100vw - 32px)
  left: 16px
  bottom: 0
  padding-bottom: 16px


.Blur
  display: none
@media screen and (max-width: 500px)
  .Blur
    display: block
    position: fixed
    z-index: 1
    left: 0
    bottom: 0
    width: 100vw
    height: 90px

    background: linear-gradient(359.51deg, #E8ECEF 15.37%, rgba(232, 236, 239, 0.48) 59.26%, rgba(232, 236, 239, 0) 99.54%)

  .ButtonWrapper
    position: fixed
    z-index: 2
    width: calc(100% - 32px)
    bottom: 0
  .ContentWrapper
    margin-bottom: 110px

@media screen and (max-width: 375px)
  .TestMainWrapper
    width: 90vw


