@import "../../../../../styles/variables"
@import "../../../../../styles/mixins"

.BackgroundImage
  width: 100vw
  top: 0
  left: 0
  position: fixed
  z-index: 0

.Failed
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column

  width: 100%
  min-height: 100vh

  background-color: $background-color


.Content
  display: flex
  flex-direction: column
  align-items: center

  text-align: center

  position: relative
  z-index: 1

.Title
  margin: 0 0 17px
  padding-bottom: 7px

  font-family: $font-dela
  font-size: 40px

  background: $font-gradient
  +gradient-text

.Board
  +center-flex
  flex-direction: column

  width: calc(100vw - 32px)
  max-width: 375px
  background: #FFFFFF
  border-radius: 25px
  padding: 24px 0

.BoardText
  width: calc(100vw - 76px)
  max-width: 331px
  text-align: center

  font-family: $font-mont
  font-weight: 500
  font-size: 16px

  color: $font-color
  margin: 0 0 20px


.ButtonWrapper
  width: calc(100vw - 76px)
  max-width: 331px

.Button
  font-family: $font-dela
  font-size: 24px

  color: #FFFFFF

@media screen and (max-width: 350px)
  .Button
    font-size: 22px