@import "../../../styles/variables"
@import "../../../styles/mixins"

.TestTask
  display: flex
  align-items: center
  flex-direction: column
  min-height: 100vh
  background: $background-color
.BackgroundImage
  width: 100vw
  height: 100px

.TestQuestion
  width: calc(100vw - 32px)
  margin: 15px 0 0 0px

  p
    padding-bottom: 5px
    margin: 0
    font-family: $font-dela
    font-size: 32px
    background: $font-gradient
    +gradient-text
    text-align: center


.TestQuestionSubTitle
  margin: 4px 0 0

  font-family: $font-mont
  font-weight: 600
  font-size: 16px
  color: $font-color

  text-align: center
  width: 100vw

.adaptiveAnswers
  display: flex
  flex-wrap: wrap
  width: calc(100vw - 32px)
  margin: 15px 0 0 0

.answer
  +center-flex
  p
    margin: 0
    margin: 0 10px
    font-family: $font-mont
    font-weight: 600
    font-size: 16px
    text-align: center
    color: #FFFFFF

  margin: 10px
  padding: 20px 0
  box-sizing: border-box
  min-width: 162px
  width: 100%

  background: $accent-gradient-purple
  border-radius: 20px