@import "../../../styles/variables"
@import "../../../styles/mixins"

.onBoard
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column

  width: 100%
  min-height: 100vh

  background-color: $background-color

.BackgroundImage
  width: 100vw
  top: 0
  left: 0
  position: fixed
  z-index: 0

.onBoardTitle, .onBoardText
  margin: 0

.onBoardTitle
  font-family: $font-dela
  font-size: 32px
  text-align: center
  background: $accent-gradient-purple
  margin-bottom: 5px
  +gradient-text

.onBoardText
  font-family: $font-mont
  font-size: 16px
  font-weight: 600
  color: $font-color
  line-height: 120%
  margin-bottom: 24px
  text-align: center
  width: 100%

.ContentWrapper
  position: relative
  z-index: 1
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column

  width: calc(100vw - 32px)
  max-width: 375px
  padding: 16px

.ButtonWrapper
  position: fixed
  z-index: 1
  display: flex
  width: calc(100vw - 32px)
  left: 16px
  bottom: 0
  padding-bottom: 16px

.Button
  font-family: $font-dela
  font-size: 28px
  margin: 0
  padding: 16px 0 18px
  color: #FFFFFF

.sberCat
  position: absolute
  z-index: 1
  left: 50%
  transform: translateX(-50%)
  bottom: 50px

@media screen and (max-width: 900px)
  .BackgroundImage
    display: none
@media screen and (max-width: 500px)
  .BackgroundImage
    display: block

@media screen and (max-height: 650px)
  .sberCat
    width: 132px
    bottom: 70px