@import "../../../../styles/variables"
@import "../../../../styles/mixins"

.Header
  display: flex
  align-items: center
  justify-content: space-between
  width: calc(100vw - 32px)
  flex-direction: row

  position: absolute
  z-index: 9




.TestCounter
  +center-flex

  background: #FFFFFF

  width: 71px
  height: 52px

  border-radius: 15px
  box-shadow: 2px 2px 15px -4px rgba(34, 41, 54, 0.1)
  p

    font-family: $font-mont
    font-size: 20px
    background: $font-gradient
    +gradient-text

.Logo
  +center-flex

