@import "../../../styles/variables"
@import "../../../styles/mixins"

.TestResult
  display: flex
  align-items: center
  flex-direction: column

  min-height: 100vh
  background: $background-color
.BackgroundImage
  width: 100vw
  height: 204px

.BottomImage
  position: fixed
  width: 100vw
  bottom: 0
.topImage
  position: absolute
  width: 100vw
  height: 204px
  top: 0
  left: 50%
  transform: translateX(-50%)

.Result
  position: absolute
  top: 110px
  left: 50%
  transform: translateY(-50%) translateX(-50%)
  +center-flex
  padding: 0 12px
  border-radius: 15px

  background: #FFFFFF
  height: 52px
  width: 86px

  p
    font-family: $font-mont
    font-size: 28px
    font-weight: 700

    background: $accent-gradient-purple
    +gradient-text

.OutlineButton, .Button
  margin: 0
  padding: 15px 0 18px

.OutlineButton
  font-family: $font-dela
  font-size: 28px

  background: $accent-gradient-purple
  +gradient-text

.Button
  font-family: $font-dela
  font-size: 28px
  color: #FFFFFF

.ButtonWrapper
  margin-top: 17px
  margin-bottom: 25px

.TestResultContentWrapper

  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  width: 100vw

.TestResultTitle
  margin-top: 15px
  width: 293px
  text-align: center

.TestResultTitleTitle
  display: flex
  justify-content: center
  font-family: $font-dela
  font-size: 32px
  width: 293px

  color: $secondary-color
  margin: 0
.TestResultTitleText
  margin: 10px 0 0
  font-family: $font-mont
  font-weight: 500
  font-size: 14px


  color: $font-color

  text-align: center
  width: 100%

.giftWrapper
  width: 100vw
  margin-bottom: 170px


.giftTitle
  margin: 0
  text-align: center
  font-family: $font-mont
  font-weight: 600
  font-size: 16px

  background: $accent-gradient-purple
  +gradient-text

.giftText
  text-align: center
  width: 223px
  margin: 2px auto 0


  font-family: $font-mont
  font-weight: 500
  font-size: 12px

  color: $font-color

.resultAccent
  width: 263px
  position: absolute
  transform: translateY(-30px) translateX(40px)

.sberCat
  position: absolute
  z-index: 0
  left: 50%
  transform: translateX(-50%)

.Buttons
  position: relative
  z-index: 1

.BottomContent
  position: fixed
  bottom: 0px
  left: 50vw
  transform: translateX(-50%)

@media screen and (max-height: 810px)
  .BottomContent
    margin-top: 20px
    position: relative

  .resultAccent
    left: 50vw

    transform: translateY(-30px) translateX(-50%)

@media screen and (max-width: 1024px)
  .BottomImage
    display: none
@media screen and (max-width: 510px)
  .BottomImage
    display: block

@media screen and (max-width: 350px)
  .topImage
    height: 170px
  .BackgroundImage
    height: 170px