@import "~normalize.css"
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap')

@import "mixins"
@import "variables"

@include font-face("Dela Gothic One", "fonts/DelaGothicOne", normal)

\:root
  --accent-gradient-purple: linear-gradient(180deg, #4A25DD 0%, #8D7FF5 100%)
  --accent-gradient-orange: linear-gradient(180deg, #FB8B3B 0%, #FFC452 100%)
  --accent-gradient-pink: linear-gradient(180deg, #D12783 0%, #FF89AC 100%)
  --accent-gradient-green: linear-gradient(180deg, #068DAD 0%, #94E3AD 100%)
  --white: #ffffff

html
  overflow-x: hidden
body
  position: relative
  background: $background-color

a
  cursor: pointer
#root
  display: flex
  justify-content: center
  background: $background-color
