@import "../../../styles/variables"
@import "../../../styles/mixins"

.TestVideo
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column
  background: $background-color

  min-height: 100vh
  width: 100%

.VideoImage
  width: 100vw
  top: 0
  left: 0
  position: fixed

.ContentWrapper
  position: relative
  z-index: 1

  display: flex
  flex-direction: column
  align-items: center

.VideoTitle
  font-family: $font-dela
  font-size: 24px
  text-align: center
  width: 90%

  background: $font-gradient
  padding-bottom: 7px
  +gradient-text
  margin: 0

.VideoFrame
  margin-top: 12px
  background: $font-gradient
  width: 100vw
  min-height: 262px
  height: 40vh
  border-radius: 30px

.Prompt
  margin-top: 15px
  margin-bottom: 15px
  max-width: 346px
  +center-flex
  width: calc(100vw - 32px)

  padding-bottom: 20px
  padding-top: 15px

  background-size: contain
  background-image: url("../../../img/background/borderWhite.svg")
  background-repeat: no-repeat
  p
    margin: 0
    font-family: $font-mont
    color: $font-color
    font-size: 14px
    text-align: center
    max-width: 312px
    width: calc(100vw - 70px)
.ButtonWrapper
  position: fixed
  z-index: 1
  display: flex
  width: calc(100vw - 32px)
  bottom: 0
  padding-bottom: 16px

.Button
  font-family: $font-dela
  color: #FFFFFF
  font-size: 24px
  margin: 20px

@media screen and (max-width: 630px)
  .ButtonWrapper
    position: relative
  .ContentWrapper
    margin-top: 60px

  .VideoFrame
    min-height: 260px
@media screen and (max-width: 600px)
  .ButtonWrapper
    position: fixed
  .ContentWrapper
    margin-top: 0px

  .VideoFrame
    min-height: 243px

@media screen and (max-width: 374px)
  .VideoFrame
    min-height: 230px
  .Prompt
    p
      width: calc(100vw - 80px)
      font-size: 10px