@import "../../../styles/variables"
@import "../../../styles/mixins"

.Stat
  +center-flex
  flex-direction: column
  width: 100%
  height: 100%
  border-radius: 25px

  background-image: url("../../../img/background/stat.svg")
  background-size: cover
  background-repeat: no-repeat
  animation: in-left 0.7s ease both
  p
    color: #FFFFFF

    width: 90%
    font-family: $font-dela
    font-size: 20px
    line-height: 120%

    text-align: center
    margin: 0
  a
    display: flex
    align-items: center

    font-family: $font-mont
    font-size: 14px
    color: #FFFFFF

    text-decoration: none
    margin-top: 10px

    svg
      margin-right: 5px
      transform: rotate(180deg)
.Profile
  display: flex
  justify-content: center
  align-items: center
  flex-direction: column
  min-height: 100vh
  padding-bottom: 30px

  background: $background-color
.AvatarWrapper
  width: 100%
  +center-flex
  border-radius: 50%
  img
    border-radius: 50%
.Avatar
  width: 64px
  height: 64px

.BackgroundImage
  width: 100vw
  top: 0
  left: 0
  position: fixed
  z-index: 0

.Title

  text-align: center
  line-height: 100%
  font-family: $font-dela
  font-size: 40px
  margin: 1px 0 12px

  background: $font-gradient
  +gradient-text

.LinkRate
  +center-flex
  text-decoration: none

  font-family: $font-mont
  font-weight: 500
  font-size: 14px
  color: #313234

  svg
    margin-left: 6px

.Info
  +center-flex
  width: 100%
  height: 144px

  background: #FFFFFF
  border-radius: 25px

  margin-top: 20px
  margin-bottom: 15px
  overflow-x: hidden
  
.Border
  +center-flex
  width: 344px
  height: 118px

  background-image: url("../../../img/background/border.svg")

.InfoText
  text-align: center
  font-size: 20px
  line-height: 120%
  font-family: $font-dela

  background: $font-gradient
  padding-bottom: 7px
  +gradient-text

.Button
  +center-flex
  font-family: $font-mont
  font-weight: 600
  font-size: 16px

  padding-bottom: 18px
  padding-top: 21px


  color: #FFFFFF

.ButtonIconWrapper
  margin-right: 20px

@media screen and (max-width: 350px)
  .Title
    font-size: 32px

  .Border
    width: 300px
    height: 103px
    background-size: contain

@keyframes in-left
  0%
    transform: translateX(300px)
  100%
    transform: translateX(0)