@import "../../../../styles/variables"
@import "../../../../styles/mixins"

.VideoItem
  +center-flex
  flex-direction: column

  width: calc(100% - 32px)
  max-width: 375px
  padding: 16px 0

  background: #FFFFFF
  border-radius: 25px
  margin-bottom: 20px


.Title
  width: 262px
  text-align: center

  font-family: $font-dela
  font-size: 20px
  line-height: 120%

  padding: 5px 0
  background: $font-color

  margin: 0 0 13px
  +gradient-text

.Video
  width: calc(100% - 40px)
  height: 197px
  border-radius: 20px

.Button
  margin: 0
  padding: 22px 0
  font-family: $font-dela
  font-size: 24px
  color: #FFFFFF

.AccentButton
  margin-top: 16px
  width: calc(100% - 40px)
