@import "../../../../styles/variables"
@import "../../../../styles/mixins"

.Footer
  +center-flex

  position: fixed
  width: 181px
  height: 65px

  bottom: 25px
  left: 50vw
  transform: translateX(-50%)

  background: white
  border-radius: 15px
  z-index: 100

.IconButton
  width: 85px
  height: 60px
  +center-flex

  opacity: .9

  cursor: pointer

.Separator
  display: block
  width: 2px
  height: 42px

  background: $font-color
  opacity: .1

.Blur
  position: fixed
  z-index: 99
  left: 0
  bottom: 0
  width: 100vw
  height: 90px

  background: linear-gradient(359.51deg, #E8ECEF 15.37%, rgba(232, 236, 239, 0.48) 59.26%, rgba(232, 236, 239, 0) 99.54%)