@import "../../../styles/variables"
@import "../../../styles/mixins"

.Subscribe
  display: flex
  align-items: center
  justify-content: center
  flex-direction: column

  width: 100%
  min-height: 100vh

  background-color: $background-color

.ButtonWrapper
  position: fixed
  z-index: 1
  display: flex
  align-items: center
  flex-direction: column


  width: calc(100vw - 32px)

  left: 16px
  bottom: 0
  padding-bottom: 16px

.Button
  font-family: $font-dela
  font-size: 28px
  margin: 0
  padding: 16px 0 18px
  color: #FFFFFF

.Link
  width: fit-content
  color: $secondary-color
  font-family: $font-mont
  font-weight: 500
  font-size: 16px
  margin: 25px 0 10px 7px

  text-align: center

.Top
  margin: 0

  position: fixed
  z-index: 1
  display: flex
  align-items: center
  flex-direction: column


  width: calc(100vw - 32px)
  left: 16px
  top: 0
  padding-top: 16px

  p
    text-align: center

    font-family: $font-dela
    font-size: 24px
    background: $accent-gradient-purple
    +gradient-text

.SubscribeTitle
  width: calc(100vw - 32px)
  max-width: 375px

  padding-bottom: 7px
  text-align: center

  font-family: $font-dela
  font-size: 28px

  margin: 0
  background: $font-gradient
  +gradient-text

.SubscribeText
  width: calc(100vw - 32px)
  max-width: 375px

  text-align: center

  font-family: $font-mont
  font-size: 16px
  font-weight: 500

  color: $font-color

  margin: 12px 0 50px

.Check
  width: 130px
  height: 140px
  margin-bottom: 40px
